.based-block {
  position: relative;
}
.video-banner-block {
  .video-container {
    position: relative;
  }
  .video {
    position: relative;
    height: 0;
    padding-bottom: 56.25%;
    text-align: center;
    overflow: hidden;
  }
  .html5 {
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 2;
    overflow: hidden;
    background-color: $gray;
  }
  .video-background {
    background: url('#{$img-path}/thumb-video.jpg') no-repeat top center;
    background-size: cover;
    width: 100%;
    height: 100%;
    z-index: 3;
    position: absolute;
    top: 0;
    &:before {
      content: '';
      position: absolute;
      z-index: 4;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background: rgba(0,0,0,.52);
    }
    &.bg-transparent {
      background: transparent;
    }
  }
  .video-html5 {
    width: 100%;
    height: auto;
  }
  .content {
    position: absolute;
    bottom: 4%;
    width: 100%;
    text-align: center;
    color: $white;
    z-index: 2;
  }
  .button-play {
    display: block;
    width: 17%;
    margin: 0 auto;
    transition: all .12s;
    opacity: 0;
    &:hover {
      filter: opacity(0.8);
    }
  }
  .title {
    color: $yellow-2;
    font-family: $roboto-bold;
    font-size: rem(37);
    line-height: 1;
    text-transform: uppercase;
  }
  .sub-title {
    margin-bottom: rem(5);
    font-family: $roboto-thin;
    font-size: rem(15);
    text-transform: uppercase;
  }
  .des {
    margin-bottom: rem(5);
    font-size: rem(11);
    line-height: 1.4;
    span {
      color: $yellow-2;
      font-family: $roboto-bold;
    }
  }
  .btn-scroll-down {
    color: $white;
    font-size: rem(16);
  }
}
.download-block {
  padding: rem(20) 5%;
  .g-title {
    margin-bottom: rem(12);
  }
  .box-main {
    display: block;
    max-width: rem(480);
    margin: 0 auto;
    padding: rem(15) 6%;
    background: $light;
    min-height: rem(185);
    transition: background .3s;
    &:hover {
      background: $light-1;
    }
  }
  .icon-wrap {
    position: absolute;
    display: block;
    width: rem(55);
    height: rem(55);
    background: $cyan;
    border-radius: 50%;
    .icon {
      padding-top: rem(10);
      color: $white;
      text-align: center;
    }
    .icon-icon-apple {
      font-size: rem(31);
    }
    .icon-icon-android {
      font-size: rem(34);
    }
  }
  .content {
    margin-left: rem(70);
    .title {
      margin-top: rem(5);
      margin-bottom: rem(12);
      font-size: rem(15);
      font-family: $roboto-bold;
      color: lighten($polishGray, 15%);
    }
    .des {
      color: lighten($polishGray, 15%);
      p:not(:last-child) {
        margin-bottom: rem(5);
      }
    }
    .view-more {
      color: $blue;
      &:hover {
        opacity: .8;
      }
    }
  }
  .qr-code {
    padding-top: 20px;
    text-align: center;
    h4 {
      font-size: rem(24);
      font-family: $roboto-bold;
      color: lighten($polishGray, 15%);
    }
    img {
      margin-top: 15px;
    }
  }
}
.contact-block {
  padding: rem(20) 10% rem(30);
  background: $white-3;
  .g-title {
    margin-bottom: rem(25);
  }
  .item {
    display: block;
    max-width: rem(440);
    margin: 0 auto;
    .title {
      font-family: $roboto-bold;
      font-size: rem(14);
      line-height: 1.4;
      color: lighten($polishGray, 20%);
    }
  }
}
@media (max-width: $screen-xs-max) {
  .download-block {
    .box-main {
      margin-bottom: rem(8);
      min-height: rem(135);
    }
  }
  .contact-block {
    .item {
      &:not(:first-child) {
        margin-top: rem(15);
      }
    }
  }
}
@media (min-width: $screen-sm-min) {
  .video-banner-block {
    .video-background {
      &:before {
        background: transparent url('#{$img-path}/bg-video.jpg') no-repeat top center;
        background-size: cover;
        opacity: .2;
      }
      &:after {
        content: '';
        position: absolute;
        z-index: 5;
        left: 0;
        right: 0;
        bottom: -40%;
        width: 100%;
        height: 100%;
        background: url('#{$img-path}/content-video-bg.png') no-repeat top center;
      }
    }
    .content {
      bottom: 2%;
    }
    .button-play {
      width: 9%;
    }
    .sub-title {
      font-size: rem(34);
    }
    .title {
      font-size: rem(80);
    }
    .des {
      margin-bottom: rem(10);
      font-size: rem(16);
    }
    .btn-scroll-down {
      font-size: rem(30);
    }
  }
  .download-block {
    padding: rem(45) 4%;
    .g-title {
      margin-bottom: rem(35);
    }
    .box-main {
      max-width: 100%;
      padding: rem(40) 6%;
    }
    .icon-wrap {
      width: rem(125);
      height: rem(125);
      .icon {
        padding-top: rem(22);
      }
      .icon-icon-apple {
        font-size: rem(72);
      }
      .icon-icon-android {
        font-size: rem(78);
      }
    }
    .content {
      margin-left: rem(150);
      .title {
        margin-top: rem(8);
        margin-bottom: rem(25);
        font-size: rem(35);
      }
      .des {
        p:not(:last-child) {
          margin-bottom: rem(22);
        }
      }
    }
  }
  .contact-block {
    padding: rem(40) 12% rem(70);
    .g-title {
      margin-bottom: rem(50);
    }
    .item {
      max-width: 90%;
      .title {
        font-size: rem(32);
      }
    }
  }
  @media (max-width: $screen-sm-max) {
    .download-block {
      .box-main {
        margin-bottom: rem(20);
      }
    }
    .contact-block {
      .item {
        &:not(:first-child) {
          margin-top: rem(30);
        }
      }
    }
  }
}
@media (min-width: $screen-md-min) {
  .download-block {
    padding-bottom: rem(65);
    .based-container {
      overflow: hidden;
    }
    .g-title {
      margin-bottom: rem(65);
    }
    .item-wrap {
      margin: 0 -0.5%;
    }
    .item {
      float: left;
      width: 50%;
      padding: 0 0.5%;
    }
    .box-main {
      padding: rem(30);
    }
    .icon-wrap {
      width: rem(90);
      height: rem(90);
      .icon {
        padding-top: rem(20);
      }
      .icon-icon-apple {
        font-size: rem(43);
      }
      .icon-icon-android {
        font-size: rem(47);
      }
    }
    .content {
      margin-left: rem(110);
      .title {
        margin-top: rem(4);
        margin-bottom: rem(20);
        font-size: rem(24);
      }
      .des {
        p:not(:last-child) {
          margin-bottom: rem(20);
        }
      }
    }
    .box-content {
      margin: 0 auto;
      padding-top: rem(30);
      max-width: 60%;
      text-align: center;
      p:not(:last-child) {
        margin-bottom: rem(20);
      }
    }
  }
  .contact-block {
    padding: rem(45) 6% rem(115);
    .based-container {
      overflow: hidden;
    }
    .g-title {
      margin-bottom: rem(45);
    }
    .item-wrap {
      margin: 0 -4%;
    }
    .item {
      float: left;
      width: 50%;
      padding: 0 4%;
    }
    .item {
      max-width: 100%;
      .title {
        font-size: rem(24);
      }
    }
  }
}
@media (min-width: $screen-lg-min) {
  .video-banner-block {
    .content {
      bottom: 20%;
    }
    .title {
      font-size: rem(85);
    }
  }
}
