@font-face {
    font-family: 'robotoblack';
    src: url('#{$icon-font-path}/roboto-black-webfont.eot');
    src: url('#{$icon-font-path}/roboto-black-webfont.eot?#iefix') format('embedded-opentype'),
         url('#{$icon-font-path}/roboto-black-webfont.woff2') format('woff2'),
         url('#{$icon-font-path}/roboto-black-webfont.woff') format('woff'),
         url('#{$icon-font-path}/roboto-black-webfont.ttf') format('truetype'),
         url('#{$icon-font-path}/roboto-black-webfont.svg#robotoblack') format('svg');
    font-weight: normal;
    font-style: normal;
}
