$shadow: 2px 3px 6px 0 rgba(0,0,0,0.35);

.guide-block {
  color: $gray-3;
  background: $white-3;
  .based-container {
    overflow: hidden;
    z-index: 1;
  }
  .tab-content > .tab-pane {
    display: block;
    height: 0;
    overflow: hidden;
    visibility: hidden;
  }
  .tab-content > .active {
    height: auto;
    visibility: visible;
  }
  .bg-guide {
    position: relative;
    width: 100%;
    height: auto;
    z-index: -1;
  }
  .guide-wrap {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
  }
  .g-title {
    margin-top: rem(20);
    color: $cyan;
  }
  .btn-timeline {
    position: absolute;
    bottom: 5px;
    right: 5px;
    z-index: 99;
    &:hover {
      opacity: .8;
    }
    img {
      width: 50%;
    }
  }

  // Hide for animations
  .des,
  .chars,
  .char-kelly .box-tooltip-details,
  .visible-block,
  .visible-block .stt-2,
  .btn-toggle .ion-toggle,
  .box-tooltip,
  .modal-interact,
  // .list-dots,
  .event,
  .location,
  .modal-event {
    opacity: 0;
  }

  .guide-nav {
    position: absolute;
    padding: 0 12%;
    bottom: 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    width: 100%;
    text-align: center;
    li {
      position: relative;
      width: 33.33%;
      transition: width .25s;
      &.active a:hover {
        cursor: default;
      }
      &:not(.active) a:hover {
        background-color: black;
      }
    }
    a {
      position: relative;
      display: block;
      margin: 0 rem(3);
      padding: rem(4) rem(2);
      color: white;
      font-size: rem(10);
      letter-spacing: 1.1px;
      background-color: $gray-7;
      white-space: nowrap;
      transition: background-color .25s;
    }
    li.active {
      a {
        color: black;
        background-color: $cyan;
      }
    }
  }
  .title {
    margin-top: rem(10);
    margin-bottom: rem(10);
    font-family: $roboto-bold;
    font-size: rem(12);
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 1px;
    padding: 0 rem(20);
    line-height: 1.2;
    color: black;
  }
  .des {
    position: absolute;
    width: 100%;
    padding: 0 4%;
    color: black;
    font-size: rem(11);
    line-height: 1.25;
    text-align: center;
    &:before,
    &:after {
      content: '';
      position: absolute;
      background: transparent url('#{$img-path}/icon-quote-left.png') no-repeat center center;
      width: 10px;
      height: 8px;
      transform: translateX(-15px);
    }
    &:after {
      background-image: url('#{$img-path}/icon-quote-right.png');
      transform: translateX(3px);
    }
  }
  .list-dots {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    z-index: -1;
    img {
      width: 100%;
    }
  }
  .chars {
    position: absolute;
    .ava {
      position: relative;
      display: block;
      margin: 0 auto rem(4);
      width: rem(20);
      height: rem(20);
      border-radius: 50%;
      overflow: hidden;
      box-shadow: $shadow;
      &.ava-bright {
        box-shadow: 0 0 40px 0 $yellow;
        filter: brightness(108%);
      }
      &.ava-circle:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        border: rem(4) solid $white;
        border-radius: 50%;
        animation: avatarCircle 1s ease infinite;
      }
      img {
        width: 100%;
        height: 100%;
        border-radius: 50%;
        object-fit: cover;
        object-position: center;
      }
      .cyan-overlay {
        position: absolute;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: $cyan;
      }
    }
    .box-tooltip {
      position: absolute;
      display: table;
      bottom: 125%;
      padding: rem(3);
      background: $white;
      border-radius: rem(4);
      white-space: nowrap;
      box-shadow: $shadow;
      &:before,
      &:after {
        content: '';
        position: absolute;
        top: 97%;
        left: 50%;
        transform: translateX(-50%);
      }
      &:after {
        border-right: rem(4) solid transparent;
        border-top: rem(6) solid $white;
        border-left: rem(4) solid transparent;
      }
      .g-content {
        display: table-cell;
        vertical-align: middle;
        padding: 0 rem(2);
      }
    }
  }
  .char-arrow {
    .ava {
      padding: 5px 0 0 4px;
      font-size: rem(11.5);
      background-color: $cyan;
      color: white;
    }
  }
  .char-shared,
  .char-actual {
    .type {
      padding: 2px 7px;
      background-color: $cyan;
      color: white;
      border-radius: 2px;
      text-align: center;
      text-transform: uppercase;
      box-shadow: $shadow;
    }
  }
  .char-actual {
    .type {
      background-color: $white-2;
      color: white;
    }
  }
  .char-1 {
    top: 55%;
    left: 47%;
  }
  .char-2 {
    top: 52%;
    left: 20%;
  }
  .char-3 {
    top: 48%;
    left: 70%;
  }
  .char-4 {
    top: 70%;
    left: 62%;
  }
  .char-5 {
    top: 72%;
    left: 15%;
  }
  .char-6 {
    top: 82%;
    left: 45%;
  }

  // TAB 2
  .btn-toggle {
    .icon {
      position: absolute;
      top: 25%;
      left: 74%;
      font-size: rem(40);
      color: $cyan;
    }
    .ion-toggle {
      color: $gray-4;
    }
  }
  .visible-block {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    .bg-circle {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: rem(1500);
      height: rem(1500);
      display: block;
      background-color: white;
      background-color: rgba(255,255,255,.5);
      border-radius: 50%;
    }
    .visible-wrapper {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
    }
    .text {
      color: $cyan;
      font-size: rem(60);
      text-transform: uppercase;
    }
    &.stt-visible {
      .bg-circle {
        background-color: $cyan;
        background-color: rgba(32, 164, 204,.5);
      }
      .text {
        color: $white;
      }
    }
  }
  .tab-2 {
    .char-inter {
      top: 52%;
      left: 70%;
    }
    .char-inter-2 {
      top: 62%;
      left: 20%;
    }
  }
  .modal-interact {
    position: absolute;
    width: rem(210);
    left: 28%;
    bottom: 15%;
    box-shadow: $shadow;
    z-index: 12;
    .pos-2 {
      display: none;
    }
    &.change-pos {
      top: 39%;
      left: auto;
      right: 28%;
      bottom: auto;
    }
  }
  .user-box {
    position: relative;
    padding: rem(5);
    background: $white;
    border-radius: rem(5);
  }
  .user-ava {
    position: absolute;
    width: rem(70);
    height: auto;
    top: 0;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    border: 4px solid $white;
    z-index: 1;
    .img-circle {
      overflow: hidden;
    }
    img {
      display: block;
      max-width: 100%;
      height: auto;
    }
    .text-circle {
      position: absolute;
      padding-top: 3px;
      width: 40%;
      height: 40%;
      bottom: -5px;
      left: 50%;
      transform: translate(-50%, 50%);
      border-radius: 50%;
      background-color: $white;
      color: $gray-3;
      text-align: center;
      font-size: rem(9);
    }
    .icon {
      color: $orange;
    }
    span {
      white-space: nowrap;
      font-family: $roboto-bold;
    }
    .text-you {
      vertical-align: middle;
      padding-top: 3px;
      color: $orange;
    }
  }
  .icons-box {
    position: relative;
    text-align: right;
    .g-icon {
      display: inline-block;
      vertical-align: middle;
      padding: 0 1px;
      color: $white-4;
      font-size: rem(20);
      &.change-color {
        color: $cyan;
      }
      &.change-color-black{
        color: $black;
      }
      &.change-color-red{
        color: $red;
      }
    }
    .fas {
      font-size: rem(14);
    }
  }
  .user-info {
    position: relative;
    margin-top: 22px;
    z-index: 2;
    .name {
      font-family: $roboto-bold;
      font-size: rem(11);
      color: black;
      text-align: center;
    }
    .position {
      margin-top: 2px;
      font-family: $roboto-italic;
      font-size: rem(11);
      text-align: center;
    }
    .g-content {
      margin-top: 2px;
      .skin-green {
        color: $green;
      }
      .skin-bold {
        font-family: $roboto-bold;
      }
    }
    .user-about {
      text-align: center;
      font-style: italic;
      p {
        color: $cyan;
        font-size: rem(10);
      }
    }
    .user-details {
      padding: 0 10px;
    }
  }
  .group-btn {
    margin: 5px 0;
    text-align: center;
    .btn-interact {
      display: inline-block;
      vertical-align: bottom;
      margin: 0 2px;
      padding: 4px 10px;
      border-radius: 5px;
      background-color: $cyan-3;
      font-size: rem(10);
      color: $white;
      text-transform: uppercase;
      letter-spacing: .7px;
    }
    .icon,
    span {
      display: inline-block;
      vertical-align: middle;
    }
  }
  .change-pos {
    .pos-1 {
      display: none;
    }
    .pos-2 {
      display: block;
    }
  }

  // TAB 3
  .event {
    position: absolute;
    .thumb {
      position: relative;
      left: 50%;
      transform: translateX(-50%);
      display: inline-block;
      background-color: white;
      padding: 2px;
      border-radius: 4px;
    }
    img {
      max-width: 100%;
      margin: 0 auto;
      display: block;
    }
    .name {
      padding: 2px 7px;
      background-color: white;
      border-radius: 2px;
      font-family: "robotomedium", sans-serif;
      text-align: center;
      text-transform: uppercase;
      box-shadow: $shadow;
    }
    &:nth-child(1) {
      top: 47%;
      left: 17%;
    }
    &:nth-child(2) {
      top: 42%;
      left: 63%;
    }
    &:nth-child(3) {
      top: 67%;
      left: 22%;
    }
  }
  .location {
    position: absolute;
    .thumb {
      position: relative;
      left: 50%;
      transform: translateX(-50%);
      background: transparent url('#{$img-path}/icon-location.png') no-repeat center top;
      padding: 2px;
      padding-bottom: 10px;
      width: 60%;
      background-size: 100% auto;
    }
    img {
      max-width: 100%;
      margin: 0 auto;
      display: block;
    }
    &:nth-child(1) {
      top: 51%;
      left: 84%;
    }
    &:nth-child(2) {
      top: 58%;
      left: 40%;
    }
    &:nth-child(3) {
      top: 75%;
      left: 63%;
    }
  }
  .modal-event {
    position: absolute;
    width: 220px;
    left: 50%;
    bottom: 50%;
    margin-left: -110px;
    margin-bottom: -140px;
    z-index: 12;
    .box {
      position: relative;
      background: $white;
      border-radius: rem(5);
      overflow: hidden;
    }
    .thumb {
      img {
        width: 100%;
        display: block;
      }
    }
    .content {
      position: relative;
      color: $gray;
      font-size: rem(9);
      p {
        font-family: $roboto-bold;
        line-height: 1.4;
        letter-spacing: 0.4px;
      }
    }
    .m-row {
      display: flex;
      align-items: center;
      padding: 3px;
      &:not(:first-child) {
        border-top: 1px solid $gray;
      }
    }
    .date,
    .users {
      width: 70px;
      text-align: center;
    }
    .date {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
    }
    .month {
      text-transform: uppercase;
      color: $red-2;
      font-family: $roboto-bold;
    }
    .day {
      font-size: rem(14);
    }
    .users {
      position: relative;
      .user {
        position: relative;
        display: inline-block;
        margin: 0 2px;
        width: rem(20);
        height: rem(20);
        border-radius: 50%;
        overflow: hidden;
      }
      img {
        max-width: 100%;
        height: auto;
        border-radius: 50%;
      }
    }
    .connect-infor {
      color: $cyan;
    }
    .actions {
      justify-content: space-between;
      .action {
        padding: 0 10px;
        color: $gray-3;
      }
      .icon {
        padding-right: 5px;
        vertical-align: middle;
        font-size: rem(13);
      }
      p {
        display: inline-block;
        vertical-align: middle;
      }
    }
  }
}

// KEYFRAMES
@keyframes avatarCircle {
  0% {
    opacity: 1;
    transform: scale(.2);
  }
  50% {
    opacity: .8;
  }
  100% {
    opacity: 0;
    transform: scale(1);
  }
}
@media (max-width: $screen-xs-max) {
  .guide-block {
    .btn-toggle{
      .icon {
        left: 78%;
      }
    }
    .event {
      .thumb {
        max-width: 44px;
      }
    }
    .modal-interact {
      left: 20%;
    }
  }
}
@media (min-width: $screen-sm-min) {
  .guide-block {
    .g-title {
      margin-top: rem(45);
    }
    .btn-timeline {
      bottom: 20px;
      right: 20px;
      img {
        width: 100%;
      }
    }
    .guide-nav {
      padding: 0 20%;
      a {
        margin: 0 rem(3);
        padding: rem(6) rem(5);
        font-size: rem(14);
      }
    }
    .title {
      margin-top: rem(20);
      margin-bottom: rem(20);
      font-size: rem(24);
    }
    .des {
      font-size: rem(18);
    }
    .chars {
      .ava {
        width: rem(38);
        height: rem(38);
        margin-bottom: rem(8);
      }
      .name {
        font-size: rem(28);
      }
      .box-tooltip-details {
        position: absolute;
        display: flex;
        align-items: center;
        bottom: 120%;
        padding: 4px 7px;
        background: $white;
        border-radius: 4px;
        white-space: nowrap;
        box-shadow: $shadow; 
        &:before,
        &:after {
          content: '';
          position: absolute;
          top: 97%;
          left: 14px;
        }
        &:after {
          border-right: 5px solid transparent;
          border-top: 7px solid $white;
          border-left: 5px solid transparent;
        }
        p {
          padding-left: 5px;
          font-family: $roboto-italic;
        }
      }
      .box-tooltip {
        bottom: 120%;
        padding: rem(7);
        &:before {
          border-right-width: rem(6);
          border-top-width: rem(8);
          border-left-width: rem(6);
        }
        &:after {
          border-right-width: rem(5);
          border-top-width: rem(7);
          border-left-width: rem(5);
        }
        .g-content {
          padding: 0 rem(4);
        }
      }
    }
    .char-arrow {
      .ava {
        padding: 8px 0 0 8px;
        font-size: rem(23);
      }
    }
    .char-style-1 {
      .ava {
        width: rem(40);
        height: rem(40);
        padding-top: rem(8);
        padding-left: rem(7);
        font-size: rem(26);
      }
    }
    .char-style-2 {
      .ava {
        width: rem(44);
        height: rem(44);
        padding: rem(3);
      }
    }

    // TAB 2
    .tab-2 {
      .char-inter-2 {
        left: 28%;
      }
    }
    .modal-interact {
      width: rem(420);
      left: 35%;
      &.change-pos {
        top: 40%;
        right: 30%;
      }
    }
    .user-box {
      padding: rem(10);
      border-radius: rem(10);
    }
    .user-ava {
      width: rem(130);
      border-width: rem(10);
      .text-circle {
        font-size: rem(14);
      }
      .icon {
        display: block;
        padding-bottom: 2px;
        font-size: rem(16);
      }
    }
    .icons-box {
      .g-icon {
        padding: 0 5px;
        font-size: rem(38);
      }
      .fas {
        font-size: rem(30);
      }
    }
    .user-info {
      margin-top: 30px;
      .name {
        font-size: rem(20);
      }
      .position {
        font-size: rem(18);
      }
      .g-content {
        margin-top: 6px;
        p {
          line-height: 1.4;
        }
      }
      .user-about {
        p {
          font-size: rem(14);
        }
      }
      .user-details {
        padding: 0 20px;
      }
    }
    .group-btn {
      margin: 10px 0;
      .icon {
        font-size: rem(13);
      }
      .btn-interact {
        margin: 0 10px;
        padding: 8px 20px;
        font-size: rem(14);
        border-radius: 10px;
        &:first-child {
          padding-top: 8px;
          padding-bottom: 8px;
        }
      }
    }

    // TAB 3
    .event {
      .name {
        margin-top: 3px;
      }
    }
    .location {
      .thumb {
        padding: 3px;
        padding-bottom: 15px;
        width: 100%;
      }
      &:nth-child(1) {
        top: 47%;
        left: 84%;
      }
      &:nth-child(2) {
        top: 58%;
        left: 35%;
      }
    }
    .modal-event {
      width: 330px;
      bottom: 42%;
      margin-left: -165px;
      margin-bottom: -125px;
      .content {
        font-size: rem(14);
        p {
          letter-spacing: 0.6px;
        }
      }
      .m-row {
        padding: 6px;
      }
      .day {
        font-size: rem(24);
      }
      .actions {
        padding-top: 8px;
        padding-bottom: 8px;
        .icon {
          font-size: rem(19);
        }
      }
    }
  }
}
@media (min-width: $screen-md-min) {
  .guide-block {
    .bg-guide {
      height: rem(735);
      width: auto;
      left: 50%;
      transform: translateX(-50%);
    }
    .guide-nav {
      bottom: inherit;
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      padding: 0;
      flex-direction: column;
      li {
        width: 90px
      }
      a {
        margin: rem(4) 0;
        padding-top: rem(8);
        padding-bottom: rem(8);
        font-size: rem(13);
        letter-spacing: inherit;
      }
      li.active {
        width: 120px;
      }
    }
    .des {
      padding: 0 16%;
    }

    // TAB 2
    .modal-interact {
      &.change-pos {
        top: 46%;
        right: 32%;
      }
    }
    .user-info {
      .g-content {
        margin-top: 10px;
        p {
          line-height: 1.6;
        }
      }
      .user-about {
        p {
          font-size: rem(14);
        }
      }
    }
    .group-btn {
      margin: 18px 0;
    }

    // TAB 3
    .event {
      &:nth-child(1) {
        left: 23%;
      }
      &:nth-child(2) {
        left: 62%;
      }
      &:nth-child(3) {
        left: 26%;
      }
    }
    .location {
      &:nth-child(1) {
        top: 47%;
        left: 76%;
      }
      &:nth-child(2) {
        top: 61%;
        left: 36%;
      }
      &:nth-child(3) {
        top: 77%;
        left: 62%;
      }
    }
    .modal-event {
      .content {
        font-size: rem(11);
      }
      .day {
        font-size: rem(18);
      }
      .actions {
        .icon {
          font-size: rem(15);
        }
      }
    }
  }
}