%button {
  display: inline-block;
  transition: all .15s linear;
  text-align: center;
  white-space: nowrap;
}
.btn-general {
  @extend %button;
  padding: rem(6) rem(22);
  border: none;
  color: $white;
  background: $cyan;
  font-size: rem(12);
  font-family: $roboto-bold;
  text-transform: uppercase;
  &:hover, &:active, &:focus {
    color: $white;
    background: $gray;
  }
}
@media (min-width: $screen-sm-min) {
  .btn-general {
    padding: rem(11) rem(53);
    font-size: rem(25);
  }
}
@media (min-width: $screen-md-min) {
  .btn-general {
    padding: rem(8) rem(38);
    font-size: rem(18);
  }
}
