.form-group {
  margin-bottom: rem(10);
}

.form-control {
  height: rem(24);
  padding: rem(2) rem(7);
  color: $gray-2;
  border-color: $white;
  border-radius: 0;
  box-shadow: none;

  &.add-border {
    border: 1px solid $blue;
  }
}

textarea.form-control {
  padding-top: rem(10);
  min-height: rem(110);
}

.error-mes {
  margin-top: rem(5);
  color: $red;
}

@media (min-width: $screen-sm-min) {
  .form-group {
    margin-bottom: rem(20);
  }

  .form-control {
    height: rem(50);
    padding: rem(10) rem(15);
  }

  textarea.form-control {
    padding-top: rem(14);
    min-height: rem(234);
  }
}

@media (min-width: $screen-md-min) {
  .form-group {
    margin-bottom: rem(12);
  }

  .form-control {
    height: rem(34);
    padding: rem(8) rem(12);
  }

  textarea.form-control {
    padding-top: rem(12);
    min-height: rem(165);
  }
}

// fix zoom when focus form on iOS
@media screen and (-webkit-min-device-pixel-ratio:0) {
  select,
  textarea,
  input {
    font-size: rem(16);
  }
}
