%v-middle {
  display: table-cell;
  vertical-align: middle;
}
.backdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: $blue;
  z-index: 100;
}
.loader {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%,-50%);
  z-index: 101;
  opacity: 1;
  .loader-img {
    display: block;
    width: 300px;
    height: auto;
  }
}
.top-mes {
  position: fixed;
  display: none;
  font-size: rem(11);
  top: rem(42);
  padding: rem(15);
  width: 100%;
  text-align: center;
  background: $yellow;
  color: lighten($polishGray, 20%);
  text-transform: uppercase;
  letter-spacing: 2px;
  z-index: 98;

  a{
    text-decoration: underline !important;
    color: $cyan;
  }

  span {
    &.skin-primary1 {
      color: $cyan;
    }
    &.skin-secondary8 {
      color: $green;
    }
    &.skin-bold {
      font-weight: bold;
    }
    &.skin-italicText{
      font-style: italic;
    }
  }
}
.header-block {
  position: fixed;
  width: 100%;
  // background: $cyan;
  background: $blue;
  z-index: 99;
  .based-container {
    height: rem(42);
  }
  .logo {
    position: relative;
    display: table;
    height: 100%;
    a {
      position: relative;
      @extend %v-middle;
    }
    img {
      height: rem(24);
      width: auto;
    }
  }
  .slogan {
    position: absolute;
    bottom: rem(2);
    color: $white;
    font-size: rem(9);
    white-space: nowrap;


  }
  .main-nav-wrap {
    li {
      a {
        transition: all .15s;
        color: $blue-2;
        font-size: rem(11);
        text-transform: uppercase;
        letter-spacing: .04em;
      }
      &.current {
        a {
          color: $white;
        }
        &:hover a {
          cursor: default;
        }
      }
    }
  }
}
.nav-toggle {
  position: absolute;
  z-index: 100;
  height: 100%;
  top: 0;
  left: 0;
  padding: rem(14);
  background: none;
  border: none;
  cursor: pointer;
  outline: none;
  .icon-menu {
    position: relative;
    display: block;
    width: rem(21);
    height: rem(14);
    span {
      position: absolute;
      display: inline-block;
      left: 0;
      width: 100%;
      height: 2px;
      background: $white;
      &:nth-child(1) {
        top: 0;
      }
      &:nth-child(2),
      &:nth-child(3) {
        top: rem(6);
      }
      &:nth-child(4) {
        bottom: 0;
      }
    }
  }
  &.opened {
    .icon-menu span {
      &:nth-child(1) {
        animation: 0.25s ease-in-out 0s normal forwards 1 firstBar;
      }
      &:nth-child(2) {
        animation: 0.25s ease-in-out 0s normal forwards 1 secondBar;
      }
      &:nth-child(3) {
        animation: 0.25s ease-in-out 0s normal forwards 1 thirdBar;
      }
      &:nth-child(4) {
        animation: 0.25s ease-in-out 0s normal forwards 1 fourthBar;
      }
    }
  }
  &.closed {
    .icon-menu span {
      &:nth-child(1) {
        animation: 0.25s ease-in-out 0s normal forwards 1 firstBarReverse;
      }
      &:nth-child(2) {
        animation: 0.25s ease-in-out 0s normal forwards 1 secondBarReverse;
      }
      &:nth-child(3) {
        animation: 0.25s ease-in-out 0s normal forwards 1 thirdBarReverse;
      }
      &:nth-child(4) {
        animation: 0.25s ease-in-out 0s normal forwards 1 fourthBarReverse;
      }
    }
  }
}

// KEYFRAMES
@keyframes firstBar {
  0% {
    top: 0;
    transform: scaleX(1) translateY(0);
  }
  100% {
    top: 50%;
    transform: scaleX(0) translateY(-50%);
  }
}
@keyframes firstBarReverse {
  0% {
    top: 50%;
    transform: scaleX(0) translateY(-50%);
  }
  100% {
    top: 0;
    transform: scaleX(1) translateY(0);
  }
}
@keyframes secondBar {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(-45deg);
  }
}
@keyframes secondBarReverse {
  0% {
    transform: rotate(-45deg);
  }
  100% {
    transform: rotate(0);
  }
}
@keyframes thirdBar {
  0% {
    transform: rotate(0);
  }
  100% {
    transform: rotate(45deg);
  }
}
@keyframes thirdBarReverse {
  0% {
    transform: rotate(45deg);
  }
  100% {
    transform: rotate(0);
  }
}
@keyframes fourthBar {
  0% {
    bottom: 0;
    transform: scaleX(1) translateY(0);
  }
  100% {
    bottom: 50%;
    transform: scaleX(0) translateY(50%);
  }
}
@keyframes fourthBarReverse {
  0% {
    bottom: 50%;
    transform: scaleX(0) translateY(50%);
  }
  100% {
    bottom: 0;
    transform: scaleX(1) translateY(0);
  }
}

// Only mobile
@media (max-width: $screen-sm-max) {
  .loader {
    .loader-img {
      width: 160px;
    }
  }
  .header-block {
    border-bottom: 1px solid $cyan-1;
    .logo {
      margin: 0 auto;
      padding-left: 2%;
      img {
        margin-top: rem(-7);
      }
    }
    .slogan {
      left: 46%;
      transform: translateX(-50%);
    }
    .main-nav {
      min-width: 150px;
      margin-top: 1px;
      background: $cyan;
      display: none;
      &.inline {
        display: inline-block;
      }
    }
    .main-nav-wrap {
      li {
        a {
          display: block;
          padding: rem(19) rem(12);
        }
        &:not(:last-child) {
          a {
            border-bottom: 1px solid $cyan-2;
          }
        }
      }
    }
  }
  // Only tablet
  @media (min-width: $screen-sm-min) {
    .header-block {
      border-bottom-width: 2px;
      .logo {
        img {
          margin-top: rem(-10);
        }
      }
      .slogan {
        bottom: rem(4);
        font-size: rem(14);
      }
      .main-nav-wrap {
        li {
          a {
            padding: rem(40) rem(28);
          }
          &:not(:last-child) {
            a {
              border-bottom-width: 2px;
            }
          }
        }
      }
    }
  }
}
@media (min-width: $screen-sm-min) {
  .top-mes {
    top: rem(84);
    padding: rem(25);
  }
  .header-block {
    .based-container {
      height: rem(84);
    }
    .logo {
      img {
        height: rem(57);
      }
    }
    .main-nav-wrap {
      li {
        a {
          font-size: rem(24);
        }
      }
    }
  }
  .nav-toggle {
    padding: rem(25);
    .icon-menu {
      width: rem(46);
      height: rem(32);
      span {
        height: 4px;
        &:nth-child(2),
        &:nth-child(3) {
          top: rem(14);
        }
      }
    }
  }
}
@media (min-width: $screen-md-min) {
  .header-block {
    .based-container {
      padding-left: 3%;
      padding-right: 3%;
    }
    .logo {
      float: left;
    }
    .slogan {
      bottom: rem(16);
      left: rem(150);
      //left: rem(52);
      font-size: rem(14);

      //bottom: 1.1em;
      //left: 11em;
      //font-size: .875rem;
    }
    .main-nav {
      height: 100%;
      display: table;
      float: right;
    }
    .main-nav-wrap {
      display: table-cell;
      vertical-align: middle;
      & > li {
        position: relative;
        display: inline-block;
        padding: 0 rem(12);
        &:not(.current):hover > a {
          opacity: .85;
        }
        a {
          font-size: rem(20);
        }
      }
    }
  }
}
@media (min-width: $screen-lg-min) {
  .header-block {
    .based-container {
      padding-left: 5%;
      padding-right: 5%;
    }
    .main-nav-wrap {
      & > li {
        padding: 0 rem(22);
      }
    }
  }
}
