@font-face {
  font-family: 'icomoon';
  src:  url('#{$icon-font-path}/icomoon.eot?6a6f4h');
  src:  url('#{$icon-font-path}/icomoon.eot?6a6f4h#iefix') format('embedded-opentype'),
    url('#{$icon-font-path}/icomoon.ttf?6a6f4h') format('truetype'),
    url('#{$icon-font-path}/icomoon.woff?6a6f4h') format('woff'),
    url('#{$icon-font-path}/icomoon.svg?6a6f4h#icomoon') format('svg');
  font-weight: normal;
  font-style: normal;
}
[class^="icon-"], [class*=" icon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'icomoon' !important;
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

