.footer-block {
  position: relative;
  width: 100%;
  padding: rem(12);
  background: $gray;
  color: $gray-2;
  overflow: hidden;
  .footer-toolbox {
    text-align: center;
    a {
      padding: 0 rem(8);
      font-size: rem(16);
      color: $white;
      transition: all .15s linear;
      &:hover, &:active, &:focus {
        color: $cyan;
      }
    }
  }
  .footer-text {
    margin-top: rem(5);
    font-size: rem(10);
    text-align: center;
    color: $gray-5;
    a {
      padding: 0 rem(4);
      color: $cyan;
      &:hover, &:active, &:focus {
        text-decoration: underline !important;
      }
    }
    span {
      color: $cyan;
    }
  }
}
.btn-to-top {
  position: fixed;
  display: block;
  padding: rem(6);
  width: rem(26);
  height: rem(26);
  right: rem(10);
  bottom: rem(10);
  // background: $cyan;
  background: $blue;
  color: $white;
  text-indent: rem(20);
  transition: all .15s linear;
  display: none;
  &:before {
    content: "";
    position: absolute;
    display: block;
    top: 50%;
    transform: translateY(-50%);
    width: rem(14);
    height: rem(14);
    background: url('#{$img-path}/arr-top.png') no-repeat center left/ auto 100%;
  }
}
@media (min-width: $screen-sm-min) {
  .footer-block {
    padding: rem(20);
    .footer-toolbox {
      a {
        padding: 0 rem(20);
        font-size: rem(26);
      }
    }
    .footer-text {
      margin-top: rem(8);
      font-size: rem(18);
      a {
        padding: 0 rem(8);
      }
    }
  }
  .btn-to-top {
    padding: rem(10);
    width: rem(46);
    height: rem(46);
    text-indent: rem(35);
    font-size: rem(16);
    &:before {
      width: rem(26);
      height: rem(26);
    }
  }
}
@media (min-width: $screen-md-min) {
  .footer-block {
    padding: rem(30) rem(25);
    .footer-text {
      position: absolute;
      margin-top: rem(5);
      top: 0;
      right: 0;
      &:last-child {
        left: 0;
        right: auto;
      }
      span {
        display: none;
      }
    }
  }
  .btn-to-top {
    &:hover {
      background: $gray;
      color: $white;
    }
  }
}
