.content-block {
  padding: rem(20) rem(25) rem(40);
  .nav-block {
    padding-left: rem(15);
  }
  .g-title-2 {
    margin-bottom: rem(12);
  }
  .content-nav {
    margin-bottom: rem(15);
    font-size: rem(15);
    line-height: 1.6;
    a {
      color: $gray;
      &:hover, &:active, &:focus {
        color: $black;
      }
    }
    li.active {
      &:before {
        border-left: solid rem(5) $cyan;
        content: "";
        margin-left: rem(-15);
        padding-left: rem(10);
      }
    }
  }
  .content {
    font-size: rem(13);
    line-height: 1.4;
    .title {
      margin-bottom: rem(15);
      font-family: $roboto-bold;
      font-size: 14px;
      &:not(:first-child) {
        margin-top: rem(5);
      }
    }
    p {
      padding-bottom: rem(10);
    }
  }
}
@media (min-width: $screen-sm-min) {
  .content-block {
    padding: rem(55) rem(60) rem(65);
    .nav-block {
      padding-left: rem(40);
    }
    .g-title-2 {
      margin-bottom: rem(30);
    }
    .content-nav {
      margin-bottom: rem(40);
      font-size: rem(34);
      li.active {
        &:before {
          border-left-width: rem(8);
          margin-left: rem(-40);
          padding-left: rem(32);
        }
      }
    }
    .content {
      font-size: rem(18);
      .title {
        margin-bottom: rem(40);
        &:not(:first-child) {
          margin-top: rem(20);
        }
      }
      p {
        padding-bottom: rem(20);
      }
    }
  }
}
@media (min-width: $screen-md-min) {
  .content-block {
    padding: rem(70) rem(40) rem(80);
    .nav-block {
      position: fixed;
      padding: 0;
    }
    .g-title-2 {
      margin-bottom: rem(12);
      padding-left: rem(20);
    }
    .content-nav {
      margin: 0;
      padding-left: rem(20);
      font-size: rem(18);
      line-height: 1.8;
      li.active {
        &:before {
          border-left-width: rem(5);
          margin-left: rem(-20);
          padding-left: rem(15);
        }
      }
    }
    .content {
      margin-left: rem(280);
    }
  }
}
