.g-title {
  font-family: $roboto-bold;
  font-size: rem(32);
  color: $cyan;
  text-align: center;
}
.g-title-2 {
  font-family: $roboto-bold;
  font-size: rem(20);
  color: $cyan;
  text-transform: uppercase;
}
.g-content,
.g-content p {
  color: lighten($polishGray, 10%);
  font-size: rem(10);
  line-height: 1.2;

  &.list-item{
    position: relative;
    padding-left: 10px;

    &:before{
      position: absolute;
      left: 0;
      content: '\2022'
    }
  }
}
@media (min-width: $screen-sm-min) {
  .g-title {
    font-size: rem(72);
  }
  .g-title-2 {
    font-size: rem(45);
  }
  .g-content,
  .g-content p {
    font-size: rem(20);
  }
}
@media (min-width: $screen-md-min) {
  .g-title-2 {
    font-size: rem(24);
  }
  .g-content,
  .g-content p {
    font-size: rem(14);
  }
}
